<template>
<li class="nav-item click_wallet" style="width:180px; margin-right: 20px;line-height: 18px;" @click="connectMetamask($store)">
    <div style="float:right">
        <div v-if="!wallet" class="connect_wallet">Connect wallet</div>
        <div v-else-if="networkId!=56" style="color:red">Wrong Network</div>
        <div v-else>Connected</div>
        <div style="color:hsla(0, 0%, 100%, .6)">{{walletShort}}</div>
    </div>
    <a class="nav-link" role="button" style="padding-top: 0;float:right">
        <img src="@/assets/image/meta.png" style="width:40px">
    </a>
</li>
</template>

<script>
import detectEthereumProvider from '@metamask/detect-provider'
import {
    mapState
} from 'vuex'
export default {
    beforeCreate() {
        this.$store.dispatch('registerWeb3')
    },
    computed: mapState({
        networkId: state => state.web3.networkId,
        wallet: state => state.web3.coinbase,
        walletShort: state => state.web3.coinbase ? state.web3.coinbase.substring(0, 4) + '...' + state.web3.coinbase.substring(state.web3.coinbase.length - 4, state.web3.coinbase.length) : ''
    }),
    mounted() {
        this.$store.dispatch('getVaultContractInstance')
    },
    methods: {
        connectMetamask: async (store) => {
            try {
                const provider = await detectEthereumProvider()
                if (provider) {
                    const accounts = await provider.request({
                        method: 'eth_requestAccounts'
                    })
                    if (accounts && accounts[0] && accounts[0] != store.state.web3.coinbase) window.location.reload()
                }
            } catch (err) {
                console.error('Connect metamask fail:', err)
            }
        }
    },
}
</script>



