<template>
<div id="app" class="wrapper" :style="'background: url(' + require('@/assets/image/bg1.jpg') + ') 50%/cover no-repeat fixed;'">
    <!-- Preloader -->
    <div class="header">
        <div class="container">
            <mobimenu />
        <aside class="main-sidebar sidebar-dark-primary elevation-4" style="background: transparent;">
            <!-- Brand Logo -->
            <a href="#/" class="brand-link">
                <img src="@/assets/image/logo.png" alt="Meta Floki" class="brand-image img-circle elevation-3" style="opacity: .8">
            </a>

            <!-- Sidebar -->
            <div class="sidebar">

                <!-- Sidebar Menu -->
                <nav class="mt-2 destop_menu">
                    <ul class="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
                        <!-- Add icons to the links using the .nav-icon class
                with font-awesome or any other icon font library -->
                        <li class="nav-item menu-open">
                            <a href="#/" :class="getClassFromRouter('staking')">
                                <i class="nav-icon fas fa-align-justify"></i>
                                <p>
                                    Home / Staking
                                </p>
                            </a>
                        </li>
                        <li class="nav-item menu-open">
                            <a href="#/nft-items" :class="getClassFromRouter('nftitems')">
                                <i class="nav-icon fas fa-exchange-alt"></i>
                                <p>
                                    NFT items
                                </p>
                            </a>
                        </li>
                        <li class="nav-item menu-open">
                            <a href="#/website" :class="getClassFromRouter('website')">
                                <i class="nav-icon fas fa-chart-bar"></i>
                                <p>
                                    Website
                                </p>
                            </a>
                        </li>
                        <li class="nav-item menu-open">
                            <a href="#/tele" :class="getClassFromRouter('tele')">
                                <i class="nav-icon fas fa-gift"></i>
                                <p>
                                    Telegram
                                </p>
                            </a>
                        </li>
                        <li class="nav-item menu-open">
                            <a href="#/twitter" :class="getClassFromRouter('twitter')">
                                <i class="nav-icon fas fa-tractor"></i>
                                <p>
                                    Twitter
                                </p>
                            </a>
                        </li>
                        <li class="nav-item menu-open">
                            <a href="#/private-sale" :class="getClassFromRouter('privatesale')">
                                <i class="nav-icon fas fa-exclamation-triangle"></i>
                                <p>
                                    Private Sale
                                </p>
                            </a>
                        </li>
                    </ul>
                </nav>
                <!-- /.sidebar-menu -->
            </div>
            <!-- /.sidebar -->
        </aside>
    <!-- Navbar -->
        <nav class="main-header navbar navbar-expand navbar-white navbar-light">
            <!-- Left navbar links -->
            <ul class="navbar-nav">
                <li class="nav-item">
                    <a :class="'nav-link'" data-widget="pushmenu" href="#" role="button" style="color: white;"><i class="fas fa-bars"></i></a>
                </li>
            </ul>

            <!-- Right navbar links -->
            <ul class="navbar-nav ml-auto">
                <metamask />
            </ul>
        </nav>
    </div>
    </div>

    <!-- /.navbar -->

    <!-- Main Sidebar Container -->


    <!-- Content Wrapper. Contains page content -->
    <div class="content-wrapper">
        <section class="content">
            <div class="container-fluid">
            <div class="container">
                <router-view />
            </div>
            </div>
        </section>
    </div>
    <!-- /.content-wrapper -->
    <footer class="main-footer text-center">
        <strong>2021 © Meta Floki</strong>
    </footer>

    <!-- Control Sidebar -->
    <aside class="control-sidebar control-sidebar-dark">
        <!-- Control sidebar content goes here -->
    </aside>
    <!-- /.control-sidebar -->
</div>
<!-- ./wrapper -->
</template>

<script>
import metamask from './meatmast.vue'
import mobimenu from './components/mobimenu.vue'

export default {
    name: 'App',
    components: {
        metamask,
        mobimenu,
    },
    methods: {
        getClassFromRouter(name) {
            if(this.$route.name == name) return 'nav-link active'
            return 'nav-link'
        }
    },
}
</script>

<style>

.container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}
.sidebar-mini .main-sidebar .nav-link{
    width:auto;
}
.sidebar-dark-primary .nav-sidebar>.nav-item>.nav-link{
    padding: 15px;
}
</style>



