<template>
    <Slide :closeOnNavigation="true">
                    <ul class="mobile">
                        <!-- Add icons to the links using the .nav-icon class
                with font-awesome or any other icon font library -->
                        <li class="mobile_menu_item">
                            <a href="#/" :class="getClassFromRouter('staking')">
                                <i class="nav-icon fas fa-align-justify"></i>
                                <p>
                                    Home / Staking
                                </p>
                            </a>
                        </li>
                        <li class="mobile_menu_item">
                            <a href="#/nft-items" :class="getClassFromRouter('nftitems')">
                                <i class="nav-icon fas fa-exchange-alt"></i>
                                <p>
                                    NFT items
                                </p>
                            </a>
                        </li>
                        <li class="mobile_menu_item">
                            <a href="#/website" :class="getClassFromRouter('website')">
                                <i class="nav-icon fas fa-chart-bar"></i>
                                <p>
                                    Website
                                </p>
                            </a>
                        </li>
                        <li class="mobile_menu_item">
                            <a href="#/tele" :class="getClassFromRouter('tele')">
                                <i class="nav-icon fas fa-gift"></i>
                                <p>
                                    Telegram
                                </p>
                            </a>
                        </li>
                        <li class="mobile_menu_item">
                            <a href="#/twitter" :class="getClassFromRouter('twitter')">
                                <i class="nav-icon fas fa-tractor"></i>
                                <p>
                                    Twitter
                                </p>
                            </a>
                        </li>
                        <li class="mobile_menu_item">
                            <a href="#/private-sale" :class="getClassFromRouter('privatesale')">
                                <i class="nav-icon fas fa-exclamation-triangle"></i>
                                <p>
                                    Private Sale
                                </p>
                            </a>
                        </li>
                    </ul>
    </Slide>
</template>

<script>
import { Slide } from 'vue-burger-menu'
export default {
  name: 'mobimenu',
      components: {
        Slide // Register your component
    },
      methods: {
        getClassFromRouter(name) {
            if(this.$route.name == name) return 'nav-link active'
            return 'nav-link'
        }
    },
}
</script>


