import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
export default new Router({
    routes: [{
        path: '/',
        name: 'staking',
        component: () => import('@/pages/staking.vue')
    },
    {
        path: '/nft-items',
        name: 'nftitems',
        component: () => import('@/pages/nftitems.vue')
    },
    {
        path: '/private-sale',
        name: 'privatesale',
        component: () => import('@/pages/presale.vue')
    }]
})



